___CSS_LOADER_EXPORT___.push([module.id, `@font-face {
  font-family: 'MergeOne';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
}

:root {
  --background-gradient-1: #235571;
  --background-gradient-2: #5e95b4;
  --panel-color: #274c62;

  --button-color: #3a7393;
  --button-color-hover: #538eb1;
  --button-cover-press: #2f6483;
  --button-color-toggled: #6fbaff;

  --text-color: white;
  --text-color-light: #a9b7c0;
}

input,
select,
textarea,
button {
  font-family: inherit;
  color: inherit;
  font-size: 1rem;
}

body {
  background: linear-gradient(0, var(--background-gradient-1) 0%, var(--background-gradient-2) 100%);
  color: var(--text-color);

  min-height: 100vh;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;

  font-family: 'MergeOne', sans-serif;
}

button {
  background-color: var(--button-color);
  border: none;
  padding: 5px 15px;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background-color: var(--button-color-hover);
}

button:active {
  background-color: var(--button-cover-press);
}

button.active {
  background-color: var(--button-color-toggled);
  cursor: default;
}
