___CSS_LOADER_EXPORT___.push([module.id, `.content-container {
  width: min(700px, 90%);
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.content-container > div {
  background-color: var(--panel-color);
  padding: 15px;
  -webkit-box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.31);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.31);
  border-radius: 5px;
}

.content-container .basic-location-info {
  grid-column: 1 / 3;
  display: grid;
  grid-template-columns: 1fr 100px 100px;

  .city-value {
    font-size: 2rem;
    grid-area: 1 / 1 / 2 / 2;
  }

  .region-value {
    grid-area: 2 / 1 / 2 / 3;
  }

  .temperature-value {
    font-size: 2rem;
    grid-area: 1 / 2 / 2 / 3;
    text-align: right;
  }

  .weather-value {
    grid-area: 2 / 2/ 3 / 3;
    text-align: right;
  }

  .weather-icon-container {
    grid-area: 1 / 3 / 3 / 4;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.content-container .bottom-section {
  grid-column: 1 / 3;
  display: flex;
  justify-content: space-between;
  padding-right: 40px;
}

.data-title {
  color: var(--text-color-light);
  margin-top: 16px;
  margin-bottom: 4px;
}

.data-title:first-child {
  margin-top: 0;
}
